
body{
    background-color:#ccc;
}
#autosuggest input {
    margin:0;
    padding:0;
    width: 104px;
    display: block;
}
#autosuggest ul {
    width: 100px;
    margin: 0;
        -webkit-margin-before: 0;
-webkit-margin-start: 0;
-webkit-margin-end: 0;
-webkit-padding-start: 0;
}
#autosuggest ul li {
    list-style-type: none;
    width: 100px;
    height: 30px;
    padding: 4px;
    background: black;
    color: white;
    font: 10px Arial;
    text-align: left;
}
#autosuggest img {
        width: 20px;
        height: 20px;
        display: inline-block;
        margin-right: 20px;
}







